import {useState} from "react"

import axios from 'axios'
import Swal from 'sweetalert2';

const initialState = {
    access: localStorage.getItem('access'),
    refresh: localStorage.getItem('refresh'),
    isAuthenticated: null,
    user: null,
    loading: false
}


const useUser =()=>{
    const [state,setState]=useState(initialState);

    const signup=async(first_name, last_name, email, password,re_password)=>{
        
        setState({
            ...state,
            loading: true
        })

        const config={
            headers:{
                'Content-Type': 'application/json'
            }
        };

        const body = JSON.stringify({
            first_name,
            last_name,
            email,
            password,
            re_password
        });

        console.log(body);

        try{
            const res = await axios.post('http://127.0.0.1:8000/auth/users/',body,config);

            if(res.status ===201){
                Swal.fire('Te enviamos un correo, por favor activa tu cuenta. Revisa el correo de spam', 'success').then((result)=>{
                    if(result.isConfirmed){
                        window.location.href="/"
                    }
                })
            }else{
                
                    Swal.fire({
                        icon: 'error',
                        title: 'Ocurrrio un error, vuelva a intentarlo!!',
                        text: 'Error al crear la cuenta',
                      });
                }
                setState({
                    ...state,
                    loading: false
                })
            
            } catch (err){
            Swal.fire({
                icon: 'error',
                title: 'Error conectando al servidor,intentelo mas tarde',
                text: err,
              });
              
            setState({
                ...state,
                loading: false
            })

        };       

    }

    const check_authenticated = async(state)=>{

        setState({
            ...state,
            loading: true
        })

        if(localStorage.getItem('access')){
            const config = {
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
                
            };

            const body = JSON.stringify({
                token: localStorage.getItem('access')
            });
        
        try {
            const res = await axios.post(`localhost:8000/auth/jwt/verify`,body,config);
            
            if(res.status === 200){
                setState({
                    ...state,
                    isAuthenticated:true
                });
            }else{
                localStorage.removeItem('access')
                localStorage.removeItem('refresh')
                setState({
                    ...state,
                    isAuthenticated: false,
                    access: null,
                    refresh: null
                });
            }
        }catch(err){
                localStorage.removeItem('access')
                localStorage.removeItem('refresh')
                setState({
                    ...state,
                    isAuthenticated: false,
                    access: null,
                    refresh: null
                })
            }
        } else {
                localStorage.removeItem('access')
                localStorage.removeItem('refresh')
                setState({
                    ...state,
                    isAuthenticated: false,
                    access: null,
                    refresh: null
                })
        }
        
    }

    const activate = async (uid, token) => {
        setState({
            ...state,
            loading: true
        })
    
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };
    
        const body = JSON.stringify({
            uid,
            token
        });
    
        try {
            const res = await axios.post(`localhost:8000/auth/users/activation/`, body, config);
        
            if (res.status === 204) {
                Swal.fire('Cuenta activada correctamente', 'success').then((result)=>{
                    if(result.isConfirmed){
                        window.location.href="/"
                    }
                });
                
            } else{

                Swal.fire({
                    icon: 'error',
                    title: 'Error activando cuenta',
                
                  });
               
            }
            setState({
                ...state,
                loading: false
            })
        }
        catch(err){
            setState({
                ...state,
                loading: false
            })
            Swal.fire({
                icon: 'error',
                title: 'Error al conectar con el servidor, intenta mas tarde.',
                text: err,
              });
        }
    };


    return{
        state,
        check_authenticated,
        activate,
        signup
    }


}

export default useUser